.projects{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.project{
  padding: 0.1rem 2rem;
  display: flex;
  justify-content: left;
  align-items: flex-start;
  flex-direction: column;
  margin: 1rem;
  background-color: #6b95bd;
  /* border-radius: .5rem; */
  width: 25rem;
  height: 14rem;
  
}
.hr{
    width: 20rem;
    height: .2rem;
    text-align: left;
    background-color: red;
    border:0;
  }

  .project h3{
    color: white;
    text-align: left;
    margin-bottom: 1rem;
  }
  .project p{
    color: #0d2439;
    margin-top: 0rem;
    text-align: left;
    font-size: 1rem;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    
  }
  span{
    color:rgb(41, 79, 114);
    font-size: medium;
  
  }

  